import React, { Component } from 'react';
//kt ni d liu trn store
import { connect } from 'react-redux';

//import  truyn d liu n Products v Product
import Products from './../components/Products';
import Product from './../components/Product';

//kim tra kiu d liu 
import PropTypes from 'prop-types';

import {actAddToCart, actChangeMessage } from './../actions/index';


// thng ny lm nhim v chnh l ly d liu trn store v v truyn cho products
class ProductsContainer extends Component {
    
    render() {
        var { productsKey } = this.props;
        // console.log(productsKey);
        return (
            //truyn d liu ra ngoi Products
            <Products>
                {this.showProduct(productsKey)}
            </Products>
        );
    }

    showProduct(productsprm){
        var result = 'Khng c sn phm';
        var {onAddToCart,onChangeMessage} = this.props;
        // console.log(onAddToCart);
        
        // console.log(productsprm);
        //dng vng lp  xut tng object 
        if(productsprm.length > 0 ){
            result = productsprm.map((product, index) =>{
                return <Product key= {index} 
                                productsK = {product}
                                onAddToCart = {onAddToCart}
                                onChangeMessage = {onChangeMessage}
                        ></Product>
            });
        }
        return result;
    }
}

//kim tra kiu d liu
ProductsContainer.propTypes = {
    productsKey : PropTypes.arrayOf(
        PropTypes.shape({
            id : PropTypes.number.isRequired,
            name : PropTypes.string.isRequired,
            image : PropTypes.string.isRequired,
            description : PropTypes.string.isRequired,
            price : PropTypes.number.isRequired,
            inventoty : PropTypes.number.isRequired,
            rating : PropTypes.number.isRequired,
        })
    ).isRequired,
    onAddToCart :PropTypes.func.isRequired,
    onChangeMessage : PropTypes.func.isRequired
}


//ly d liu store v (products.js)
const mapStateToProps = state => {
    return {
        productsKey : state.products
    }
}
const mapDispatchToProps = (dispatch, props) => {
    return {
        onAddToCart: (product) => {
            dispatch(actAddToCart(product, 1));
        },
        onChangeMessage : (message) => {
            dispatch(actChangeMessage(message));
        }
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(ProductsContainer);



