import React, { Component } from 'react';


class Cart extends Component {
    render() {
        //y l 1 dng props c bit
        // nu ko c children ny th s ko hin th cc cart con c
        var { children } = this.props;
        // console.log(children);
        
        return (
            <section className="section">
                <div className="table-responsive">
                    <table className="table product-table">
                        <thead>
                            <tr>
                                <th></th>
                                <th>Sn Phm</th>
                                <th>Gi</th>
                                <th>S Lng</th>
                                <th>Tng Cng</th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            {children}
                        </tbody>
                    </table>
                </div>
            </section>
        );
    }
}

export default Cart;



